@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #f0f0f0;
  background-image: url(/static/media/background.a160ab1c.gif);
}

input, button, textarea, label, p, span {
  font: 400 18px Roboto, sans-serif;
}

button {
  cursor: pointer;
  margin-bottom: 20px;
}

input {
  width: 100%;
  height: 60px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 0 24px;
}

form textarea {
  width: 100%;
  resize: vertical;
  min-height: 140px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 16 24px;
  line-height: 24px;
}

.button {
  width: 100%;
  height: 60px;
  background: #009578;
  border: 0;
  border-radius: 8px;
  color: #fff;
  font-weight: 700;
  margin-top: 16px;
  display: inline-block;
  text-align: center;
  -webkit-text-decoration: 18px;
          text-decoration: 18px;
  font-size: 18px;
  line-height: 60px;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
}

.button:hover {
  -webkit-filter: brightness(85%);
          filter: brightness(85%);
}

.back-link {
  display: flex;
  align-items: center;
  margin-top: 40px;
  color: #41414d;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.2s;
}

.back-link svg {
  margin-right: 8px;
}

.back-link:hover {
  opacity: 0.8;
}
.register-container {
  width: 110%;
  /* max-width: 1320px; */
  max-width: 75%;
  height: 100vh;
  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: center;
}

.register-container .content {
  width: 100%;
  padding: 96px;
  background: #f0f0f5;
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
  border-radius: 8px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.register-container .content section {
  width: 100%;
  max-width: 380px;
}

.register-container .content section h1 {
  margin: 64px 0 32px;
  font-size: 32px;
}

.register-container .content section p {
  font-size: 18px;
  color: #737380;
  line-height: 32px;
}

.register-container .content .form {
  width: 100%;
  max-width: 700px;
}

.register-container .content .form label {
  margin-top: 10px;
}

.register-container .content .form input {
  margin: 8px;
}

.register-container .content .form .upload-button {
  padding: 10px;
  height: 60px;
  color: white;
  font-weight: 700;
  text-align: center;
  -webkit-text-decoration: 18px;
          text-decoration: 18px;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
  font-size: 18px;
  background-color: #009578;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
}

.register-container .content .form .upload-button:hover {
  -webkit-filter: brightness(85%);
          filter: brightness(85%);
}

.register-container .content .form .custom-text {
  margin-left: 10px;
  color: #aaa;
}

.register-container .content .form .textAlign {
  text-align: center;
}

.register-container .content .form .load {
  float: left;
  color: #0a0a0a;
  text-align: center;
}


